<template>
    <div id="tabdel">
        <ul>
            <li>
                <div class="content" 
                    @touchstart="startDrag"
                    @touchmove="onDrag" 
                    @touchend="endDrag" 
                    :style="deleteSlider">{{defcon.title}}
                </div>
                <div class="remove" ref="remove" :style="remSlider"></div>
            </li>
        </ul>
    </div>
</template>
<script>
    export default {
        name: 'tabdel',
        data() {
            return {
                startX: 0,  //
                startY: 0,
                endX: 0,    //
                moveX: 0,   //
                disX: 0,    //
                deleteSlider: '',
                remSlider: '',
                defcon: {id: 1, title: 'woshi1'},
                isshow: false
            }
        },
        methods: {
            //touchstart
            // touchmovepreventDefault()
            // touchend
            // touchcancel
            // changedTouches  () array
            // touches: 
            // targetTouches  
            startDrag(evt) {
                evt = evt.changedTouches ? evt.changedTouches[0] : evt;
                console.info(evt.pageX, evt);
                //tounches1
                if(evt) {
                    this.startX = evt.pageX;
                    this.startY = evt.pageY;
                }
            },
            onDrag(ev) {
                ev = ev || event;
                //
                let wd = this.$refs.remove.offsetWidth;
                if(ev.touches.length == 1) {
                    this.moveX = ev.touches[0].clientX;
                    this.disX = this.startX - this.moveX;
                    //
                    if(this.disX < 0 || this.disX == 0) {
                        this.deleteSlider = "transform: translateX(0px)";
                        this.remSlider = `transform: translate3d(${wd}px, 0px, 0px)`;
                    } else {
                        //  
                        if (this.disX >= wd / 2) {
                            this.deleteSlider = `transform: translateX(- ${wd} "px)`;
                            this.remSlider = 'transform: translate3d(0px, 0px, 0px)';
                        } else {
                            this.deleteSlider = `transform: translateX(-${this.disX}px)`;
                            this.remSlider = `transform: translate3d(${this.disX}px, 0px, 0px)`;
                        }
                    }
                }
            },
            endDrag(ev) {
                ev = ev || event;
                let wd = this.$refs.remove.offsetWidth;
                if (ev.changedTouches.length == 1) {
                    let endX = ev.changedTouches[0].clientX;
                    this.disX = this.startX - endX;
                    if ((this.disX) <= ( wd / 2)) {
                        this.deleteSlider = "transform: translateX(0px)";
                        this.remSlider = `transform: translate3d(${wd}px, 0px, 0px)`;
                    } else {
                        this.deleteSlider = `transform: translateX(-${wd}px)`;
                        this.remSlider = `transform: translate3d(0px, 0px, 0px)`;
                    }
                }
            }
        }
    }
</script>
<style lang="sass" scoped type="text/sass">
    #tabdel
        ul
            li
                height: px2rem(100)
                width: 100%
                background-color: #ddd
                line-height: px2rem(100)
                border-bottom: 1px solid #fff
                position: relative
                overflow: hidden
                // user-select: none
                .content
                    width: 100%
                    text-align: center
                    color: #000
                    position: relative
                    z-index: 100
                    transition: transform .15s ease-in-out,-webkit-transform .15s ease-in-out
                .remove
                    width: px2rem(300)
                    height: px2rem(100)
                    background-color: red
                    color: #fff
                    text-align: center
                    @include font-dpr(16px)
                    border-bottom: 1px solid #ddd
                    display: inline-block
                    position: absolute
                    right: 0
                    top: 0
                    z-index: 1
                    // transition: width 1s
                    transform: translate3d(px2rem(300), 0px, 0px)
                    transition: transform .15s ease-in-out,-webkit-transform .15s ease-in-out
</style>



